import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {EnvironmentSettingsComponent} from './environment-settings/environment-settings.component';
import {UiModule} from "../ui/ui.module";
import {ComponentRegistryService} from "../core/services/components/component-registry.service";
import { EnvironmentComponent } from './environment.component';

@NgModule({
  declarations: [EnvironmentSettingsComponent, EnvironmentComponent],
  imports: [
    CommonModule,
    UiModule
  ]
})
export class EnvironmentModule {
  constructor(private componentRegistryService: ComponentRegistryService) {
    this.componentRegistryService.define('eim-environment-settings', EnvironmentSettingsComponent);
  }
}
