import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {EimResourceModel} from "../../model/resources/resource-model";

@Component({
  selector: 'eim-add-resource',
  templateUrl: './add-resource.component.html',
  styleUrls: ['./add-resource.component.scss']
})
export class AddResourceComponent implements OnInit {

  @Input()
  configs: EimResourceModel[];

  @Output()
  configChange: EventEmitter<any> = new EventEmitter<any>();

  description: string;

  selectedResources: string[];

  constructor() {
  }

  ngOnInit() {
    console.log('this config', this.configs);
  }

  resourceClick(resource: EimResourceModel) {
    this.description = resource.description;
  }

  resourceSelect() {
    this.configs.forEach((r) => {
      r.optionalSelected = this.selectedResources.includes(r.id);
    });
    const data = this.configs.map((e) => {
      return {
        id: e.id,
        optionalSelected: e.optionalSelected
      }
    });
    this.configChange.emit(data);
  }
}
