import {Component, OnInit} from '@angular/core';
import {EimResourceModel} from "../model/resources/resource-model";
import {DataService} from "../core/services/data.service";
import {FooterAction, FooterConfig, HTTPMethod} from "../model/ui/components/footer";
import {ComponentType} from "../model/config-model";
import {CustomTooltipConfig} from "../model/ui/components/custom-tooltip";
import {DynamicComponentType, ExternalActionMessage} from "../model/dynamic-component";
import {EimUiService} from "../core/services/components/eim-ui.service";

@Component({
  selector: 'eim-resources',
  templateUrl: './resources.component.html',
  styleUrls: ['./resources.component.scss']
})
export class ResourcesComponent implements OnInit {

  resources: EimResourceModel[];

  constructor(private dataService: DataService, private eimUiService: EimUiService) {
  }

  ngOnInit() {
    this.dataService.getResources().subscribe((data) => {
      this.resources = data.map((e) => e["Resource"]);
      console.log(this.resources);
    });
  }

  addResourceClick(): void {
    const opResources: EimResourceModel[] = this.resources.filter((el) => el.optional && !el.optionalSelected);
    this.showPopup(opResources);
  }

  configure(resource: EimResourceModel) {
    const action: FooterAction = {
      method: HTTPMethod.PATCH,
      url: '/resource/' + resource.id
    };
    const e: ExternalActionMessage = {
      action: DynamicComponentType.WINDOW,
      header: {
        caption: resource.name,
        closeIcon: true
      },
      footer: this.getFooter(action),
      component: {
        type: ComponentType.IFRAME,
        value: resource.uiConfig.value,
        configs: resource.configs
      }
    };
    this.eimUiService.createComponent(e).hide.subscribe(() => {
      console.log('component hide');
    });
  }

  showPopup(data: EimResourceModel[]): void {
    const e: CustomTooltipConfig = this.addResourcesActionConfiguration(data);
    this.eimUiService.createComponent(e).hide.subscribe(() => {
      console.log('component hide');
    });
  }

  getFooter(action: FooterAction): FooterConfig {
    return {
      buttons: [{
        type: 'SAVE',
        caption: 'Save',
        disabled: true,
        cls: 'main',
        action: action
      }, {
        type: 'CLOSE',
        caption: 'Cancel',
        cls: 'secondary'
      }]
    };
  }

  addResourcesActionConfiguration(data: any): CustomTooltipConfig {
    const action: FooterAction = {
      method: HTTPMethod.PATCH,
      url: '/resources'
    };
    return {
      action: DynamicComponentType.POPUP,
      target: '#add-resource-action',
      width: '700px',
      height: '500px',
      header: {
        caption: 'Select Optional Resources',
        closeIcon: true
      },
      footer: this.getFooter(action),
      component: {
        type: ComponentType.WEB_COMPONENT,
        value: "eim-add-resource",
        configs: data
      }
    };
  }
}
