import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'eim-logs',
  templateUrl: './logs.component.html',
  styleUrls: ['./logs.component.scss']
})
export class LogsComponent implements OnInit {

  private _config: string;
  config: string;

  constructor() {
  }

  ngOnInit() {
    this.waitConfigs().then((configdata: string) => {
      this._config = configdata;
      this.startInterval();
    })
  }

  startInterval() {
    setInterval(() => {
      this.config = this.config ? undefined : this._config;
      window.parent.postMessage(this.config, window.location.origin);
    }, 1000);
  }

  async waitConfigs() {
    return new Promise(((resolve) => {
      const interval = setInterval(() => {
        const _config = this.getWindowConfigs();
        if (_config) {
          clearInterval(interval);
          resolve(_config);
        }
      }, 100);
    }));
  }

  getWindowConfigs(): any {
    return window["configs"];
  }

}
